	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/f/fc44d1dfbd3910e574cdedb0f05162f65b4cb7c4.jpg\" alt=\"HTML entities &#x26;&#x27;<>&#x22;\"></p>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/triangular_flag_on_post.png?v=3\" title=\":triangular_flag_on_post:\" alt=\":triangular_flag_on_post:\" class=\"forum-image\"> Remember to use <a><strong><code>Read-Search-Ask</code></strong></a> if you get stuck. Try to pair program <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/busts_in_silhouette.png?v=3\" title=\":busts_in_silhouette:\" alt=\":busts_in_silhouette:\" class=\"forum-image\"> and write your own code <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/pencil.png?v=3\" title=\":pencil:\" alt=\":pencil:\" class=\"forum-image\"></p>\n<h3><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/checkered_flag.png?v=3\" title=\":checkered_flag:\" alt=\":checkered_flag:\" class=\"forum-image\"> Problem Explanation:</h3>\n<ul>\n<li>You have to create a program that will convert HTML entities from string to their corresponding HTML entities. There are only a few so you can use different methods.</li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 1</h2>\n<ul>\n<li>You can use regular Expressions however I didnâ€™t in this case.</li>\n</ul>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 2</h2>\n<ul>\n<li>You will benefit from a chart with all the html entities so you know which ones are the right ones to put.</li>\n</ul>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 3</h2>\n<ul>\n<li>You should separate the string and work with each character to convert the right ones and then join everything back up.</li>\n</ul>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2>Spoiler Alert!</h2>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/2/2d6c412a50797771301e7ceabd554cef4edcd74d.gif\" alt=\"warning sign\"></p>\n<p><strong>Solution ahead!</strong></p>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/beginner.png?v=3\" title=\":beginner:\" alt=\":beginner:\" class=\"forum-image\"> Basic Code Solution:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>    <span class=\"token keyword\">function</span> <span class=\"token function\">convertHTML</span><span class=\"token punctuation\">(</span>str<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token comment\" spellcheck=\"true\">// Split by character to avoid problems.</span>\n\n      <span class=\"token keyword\">var</span> temp <span class=\"token operator\">=</span> str<span class=\"token punctuation\">.</span><span class=\"token function\">split</span><span class=\"token punctuation\">(</span><span class=\"token string\">''</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n      <span class=\"token comment\" spellcheck=\"true\">// Since we are only checking for a few HTML elements I used a switch</span>\n\n      <span class=\"token keyword\">for</span> <span class=\"token punctuation\">(</span><span class=\"token keyword\">var</span> i <span class=\"token operator\">=</span> <span class=\"token number\">0</span><span class=\"token punctuation\">;</span> i <span class=\"token operator\">&lt;</span> temp<span class=\"token punctuation\">.</span>length<span class=\"token punctuation\">;</span> i<span class=\"token operator\">++</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">switch</span> <span class=\"token punctuation\">(</span>temp<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n          <span class=\"token keyword\">case</span> <span class=\"token string\">'&lt;'</span><span class=\"token punctuation\">:</span>\n            temp<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> <span class=\"token string\">'&amp;lt;'</span><span class=\"token punctuation\">;</span>\n            <span class=\"token keyword\">break</span><span class=\"token punctuation\">;</span>\n          <span class=\"token keyword\">case</span> <span class=\"token string\">'&amp;'</span><span class=\"token punctuation\">:</span>\n            temp<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> <span class=\"token string\">'&amp;amp;'</span><span class=\"token punctuation\">;</span>\n            <span class=\"token keyword\">break</span><span class=\"token punctuation\">;</span>\n          <span class=\"token keyword\">case</span> <span class=\"token string\">'>'</span><span class=\"token punctuation\">:</span>\n            temp<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> <span class=\"token string\">'&amp;gt;'</span><span class=\"token punctuation\">;</span>\n            <span class=\"token keyword\">break</span><span class=\"token punctuation\">;</span>\n          <span class=\"token keyword\">case</span> <span class=\"token string\">'\"'</span><span class=\"token punctuation\">:</span>\n            temp<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> <span class=\"token string\">'&amp;quot;'</span><span class=\"token punctuation\">;</span>\n            <span class=\"token keyword\">break</span><span class=\"token punctuation\">;</span>\n          <span class=\"token keyword\">case</span> <span class=\"token string\">\"'\"</span><span class=\"token punctuation\">:</span>\n            temp<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> <span class=\"token string\">\"&amp;apos;\"</span><span class=\"token punctuation\">;</span>\n            <span class=\"token keyword\">break</span><span class=\"token punctuation\">;</span>\n        <span class=\"token punctuation\">}</span>\n      <span class=\"token punctuation\">}</span>\n\n      temp <span class=\"token operator\">=</span> temp<span class=\"token punctuation\">.</span><span class=\"token function\">join</span><span class=\"token punctuation\">(</span><span class=\"token string\">''</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n      <span class=\"token keyword\">return</span> temp<span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n\n    <span class=\"token comment\" spellcheck=\"true\">//test here</span>\n    <span class=\"token function\">convertHTML</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"Dolce &amp; Gabbana\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h3>Code Explanation:</h3>\n<p>Explain solution here and add any relevant links</p>\n<h4>Relevant Links</h4>\n<ul>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/split' target='_blank' rel='nofollow'>str.split()</a></li>\n<li><a href='https://developer.mozilla.org/en/docs/Web/JavaScript/Reference/Global_Objects/Array/join' target='_blank' rel='nofollow'>arr.join()</a></li>\n<li><a href='https://developer.mozilla.org/en/docs/Web/JavaScript/Reference/Statements/switch' target='_blank' rel='nofollow'>switch statement</a></li>\n</ul>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/CLnP/0' target='_blank' rel='nofollow'>Run Code</a></p>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/sunflower.png?v=3\" title=\":sunflower:\" alt=\":sunflower:\" class=\"forum-image\"> Intermediate Code Solution:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>function convertHTML(str) {\n//Chaining of replace method with different arguments\n  str = str.replace(/&/g,'&amp;').replace(/</g,'&lt;').replace(/>/g,'&gt;').replace(/\"/g,'&quot;').replace(/'/g,\"&apos;\");\nreturn str;\n}\n\n// test here\nconvertHTML(\"Dolce & Gabbana\");</code></pre>\n      </div>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/CLnQ/0' target='_blank' rel='nofollow'>Run Code</a></p>\n<h3>Code Explanation:</h3>\n<p>Explain solution here and add any relevant links</p>\n<h4>Relevant Links</h4>\n<ul>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/replace' target='_blank' rel='nofollow'>str.replace()</a></li>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/RegExp' target='_blank' rel='nofollow'>Regular Expressions</a></li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rotating_light.png?v=3\" title=\":rotating_light:\" alt=\":rotating_light:\" class=\"forum-image\"> Advanced Code Solution:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>    <span class=\"token keyword\">function</span> <span class=\"token function\">convertHTML</span><span class=\"token punctuation\">(</span>str<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token comment\" spellcheck=\"true\">// Use Object Lookup to declare as many HTML entities as needed.</span>\n      htmlEntities<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span>\n        <span class=\"token string\">'&amp;'</span><span class=\"token punctuation\">:</span><span class=\"token string\">'&amp;amp;'</span><span class=\"token punctuation\">,</span>\n        <span class=\"token string\">'&lt;'</span><span class=\"token punctuation\">:</span><span class=\"token string\">'&amp;lt;'</span><span class=\"token punctuation\">,</span>\n        <span class=\"token string\">'>'</span><span class=\"token punctuation\">:</span><span class=\"token string\">'&amp;gt;'</span><span class=\"token punctuation\">,</span>\n        <span class=\"token string\">'\\\"'</span><span class=\"token punctuation\">:</span><span class=\"token string\">'&amp;quot;'</span><span class=\"token punctuation\">,</span>\n        <span class=\"token string\">'\\''</span><span class=\"token punctuation\">:</span><span class=\"token string\">\"&amp;apos;\"</span>\n      <span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n      <span class=\"token comment\" spellcheck=\"true\">//Use map function to return a filtered str with all entities changed automatically.</span>\n      <span class=\"token keyword\">return</span> str<span class=\"token punctuation\">.</span><span class=\"token function\">split</span><span class=\"token punctuation\">(</span><span class=\"token string\">''</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">map</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">function</span><span class=\"token punctuation\">(</span>entity<span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">return</span> htmlEntities<span class=\"token operator\">&lt;</span>a href<span class=\"token operator\">=</span><span class=\"token string\">'https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3 \":rocket:\"'</span> target<span class=\"token operator\">=</span><span class=\"token string\">'_blank'</span> rel<span class=\"token operator\">=</span><span class=\"token string\">'nofollow'</span><span class=\"token operator\">></span>entity<span class=\"token punctuation\">]</span> <span class=\"token operator\">||</span> entity<span class=\"token punctuation\">;</span>\n      <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">join</span><span class=\"token punctuation\">(</span><span class=\"token string\">''</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n\n    <span class=\"token comment\" spellcheck=\"true\">// test here</span>\n    <span class=\"token function\">convertHTML</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"Dolce &amp; Gabbana\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/CLnR/0' target='_blank' rel='nofollow'>Run Code</a></p>\n<h3>Code Explanation:</h3>\n<ul>\n<li>Create a object to use the Lookup functionality to easily find the characters.</li>\n<li>Split the original string by characters and use map to check for the changed html entity or use the same one. Alternatively you could use Regex <code>str.replace(/&#x26;|&#x3C;|>|\"|'/gi</code>.</li>\n<li>The a function is added which is what returns the converted entity or the original one if there is no conversion. If you go the regex route then you just have to return the matched hits. <code>return html[entity];</code></li>\n<li>Lastly we join all the characters once again.</li>\n</ul>\n<p><strong>Note</strong> that if you went the regex route then you donâ€™t need to join anything, just make sure you return the whole operation or save it to a variable and then return it.</p>\n<h4>Relevant Links</h4>\n<ul>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/split' target='_blank' rel='nofollow'>str.split()</a></li>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/map' target='_blank' rel='nofollow'>arr.map()</a></li>\n<li><a href='https://developer.mozilla.org/en/docs/Web/JavaScript/Reference/Global_Objects/Array/join' target='_blank' rel='nofollow'>arr.join()</a></li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/clipboard.png?v=3\" title=\":clipboard:\" alt=\":clipboard:\" class=\"forum-image\"> NOTES FOR CONTRIBUTIONS:</h2>\n<ul>\n<li><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/warning.png?v=3\" title=\":warning:\" alt=\":warning:\" class=\"forum-image\"> <strong>DO NOT</strong> add solutions that are similar to any existing solutions. If you think it is <strong><em>similar but better</em></strong>, then try to merge (or replace) the existing similar solution.</li>\n<li>Add an explanation of your solution.</li>\n<li>Categorize the solution in one of the following categories â€” <strong>Basic</strong>, <strong>Intermediate</strong> and <strong>Advanced</strong>. <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/traffic_light.png?v=3\" title=\":traffic_light:\" alt=\":traffic_light:\" class=\"forum-image\"></li>\n<li>Please add your username only if you have added any <strong>relevant main contents</strong>. (<img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/warning.png?v=3\" title=\":warning:\" alt=\":warning:\" class=\"forum-image\"> <strong><em>DO NOT</em></strong> <em>remove any existing usernames</em>)</li>\n</ul>\n<blockquote>\n<p>See <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/point_right.png?v=3\" title=\":point_right:\" alt=\":point_right:\" class=\"forum-image\"> <a href='http://forum.freecodecamp.com/t/algorithm-article-template/14272' target='_blank' rel='nofollow'><strong><code>Wiki Challenge Solution Template</code></strong></a> for reference.</p>\n</blockquote>","fields":{"slug":"/certificates/convert-html-entities/"},"frontmatter":{"title":"Convert HTML Entities"}}},"pathContext":{"slug":"/certificates/convert-html-entities/"}}

